/* https://www.digitalocean.com/community/tutorials/css-minimal-css-reset */
html {
  box-sizing: border-box;
  font-size: 16px;
}

*,
*:before,
*:after {
  box-sizing: inherit;
}

body,
h1,
h2,
h3,
h4,
h5,
h6,
p,
ol,
ul {
  margin: 0;
  padding: 0;
  font-weight: normal;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

.app-container {
  background-color: rgb(255, 200, 200);
  padding-left: 120px;
}

.prepare-container {
  background-color: blue;
  padding-left: 120px;
}

.page-container {
  margin: 4em;
}
.page-container#home {
  text-align: center;
}

#page-1 {
  background-color: orange;
}

nav {
  width: 100%;
  background-color: #123;
  display: flex;
  justify-content: space-around;
  align-items: center;
}
nav .nav-links {
  list-style: none;
  display: flex;
}
nav .nav-links .nav-item a {
  display: inline-block;
  text-decoration: none;
  color: white;
  padding: 1em 2em;
}
nav .nav-links .nav-item a:hover {
  color: #abc;
}
nav .nav-links .nav-item a.active {
  font-weight: bold;
}

nav.vertical {
  width: 120px;
  height: 100vh;
  flex-direction: row;
  position: absolute;
  left: 0;
  top: 0;
}
nav.vertical .nav-links {
  flex-direction: column;
}
nav.vertical .nav-links .nav-item a {
  display: block;
  padding: 4em 0;
}

.prepare-container {
  background-color: blue;
  padding-left: 120px;
}

#page-1 {
  background-color: orange;
}

#task-grid {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-rows: 1fr;
  grid-template-areas: "taskform tasklist";
}

#taskform {
  grid-area: taskform;
  max-width: 300px;
  margin: 0 auto;
}
#taskform label {
  display: block;
  width: 100%;
}

#tasklist {
  grid-area: tasklist;
}

:root {
  --primary: crimson;
  --dark: grey;
  --darker: black;
  --light: white;
}

.practice-container {
  background-color: var(--dark);
  padding-left: 120px;
  color: var(--light);
}

#page-2 {
  background-color: green;
}

.watch {
  text-align: center;
  background-color: var(--darker);
  width: 100%;
  max-width: 400px;
  margin: 4em auto;
  padding: 1rem 2rem;
  border-radius: 1rem;
}
.watch .time {
  font-size: 5rem;
  font-weight: 300;
  margin-bottom: 1rem;
}
.watch .controls {
  display: flex;
  align-items: center;
  justify-content: center;
}
.watch .controls button {
  appearance: none;
  background: none;
  border: none;
  outline: none;
  user-select: none;
  color: var(--light);
  background-color: var(--primary);
  font-size: 1.5rem;
  margin: 0 0.5rem;
  padding: 0.5 1rem;
  border-radius: 0.5rem;
  transition: 0.4s;
}
.watch .controls button {
  appearance: none;
  background: none;
  border: none;
  outline: none;
  user-select: none;
  color: var(--light);
  background-color: var(--primary);
  font-size: 1.5rem;
  margin: 0 0.5rem;
  padding: 0.5 1rem;
  border-radius: 0.5rem;
  transition: 0.4s;
}
.watch .controls button:hover {
  opacity: 0.7;
}
.watch .controls button:active {
  background-color: var(--darker);
}

.control-panel {
  background-color: var(--primary);
  color: var(--light);
  width: 100%;
  max-width: 400px;
  margin: 4em auto;
  padding: 1rem 2rem;
  border-radius: 1rem;
}
.control-panel h2 {
  text-align: left;
  padding: 1rem 1.5rem;
  background-color: var(--darker);
}

.slider-container {
  background-color: var(--dark);
  padding: 2rem 1rem;
  margin: 2em auto;
  width: 75%;
}
.slider-container .slider {
  -webkit-appearance: none;
  width: 100%;
  height: 1rem;
  background: linear-gradient(90deg, rgb(214, 214, 214) 25%, rgb(214, 214, 214) 50%);
  outline: none;
  opacity: 0.5;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
  border-radius: 1rem;
  box-shadow: 0px 1px 10px 1px black;
}
.slider-container .slider:hover {
  opacity: 1;
}
.slider-container p {
  margin-top: 2.5rem;
  opacity: 0.7;
  color: var(--primary);
}
.slider-container .slider::after {
  content: "100";
  color: #ffffff;
  font-size: 1rem;
  position: relative;
  top: 2rem;
}
.slider-container .slider::before {
  content: "0";
  color: #ffffff;
  font-size: 1rem;
  position: relative;
  top: 2rem;
}